.btn-github {
  display: flex;
  flex-direction: row;
  gap: 2px;
  font-size: .7em; /*11 px*/
  font-weight: 700;
  line-height: 1.8em;
  color: #576060;
  background: #f6f8fa;
  border: 1px solid #d5d7da;
  border-radius: 6px;
  padding: 2px 4px;
}

:root[data-color="dark"] .btn-github, :root[data-color="night"] .btn-github {
  color: #c9d1d9;
  background: #21262d;
  border: 1px solid #576060;
}

.btn-github .icon {
  transform: scale(.8); /* 18px */
}

.whatsapp-button {
  padding: 15px 30px;
  margin-top: 50px;
  margin-right: 15px;
  margin-left : 15px;
  font-size: 18px;
  color: white;
  background-color: #387e28;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  text-align: center;
  text-decoration: none;
  display: inline-block;
}
.whatsapp-button::before {
  content: '\f095'; /* Ícone do Font Awesome para o telefone */
  font-family: FontAwesome;
  margin-right: 10px;
}

:root[data-color="dark"] .btn-buymeacoffee, :root[data-color="night"] .btn-buymeacoffee {
  box-shadow: 0px 2px 3px rgba(255, 255, 255, 0.1);
}

.btn-close {
  background: var(--background-fg);
  border: 1px dotted var(--border-color);
  border-radius: 4px;
  cursor: pointer;
}
